.habitGroupEntry-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding:18px 0px 18px 35px;
    gap: 10px;

    width: 100%;
    height: 56px;

    background: #25232A;
    border-bottom:1px solid #4f4f50;
    &__entry {
        position: absolute;
        padding: 8px 8px 8px 16px;
      }
}
.habitGroupEntry-container:last-child {
  border-bottom:0px;
}
.checkboxes{
  display: grid;
  grid-auto-flow: column;
  gap: 106px;
  margin-left: 358px;
  margin-right: auto;
}